.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    border-radius: 10px;
    padding: 10px;
    box-shadow: 0 1px 10px black;
    background-color: white;
}
.info{
    display: flex;
    flex-direction: row;
}